<template>
  <div>
    <div class="tableTitle">
        <div class="tableTitle_left"></div>
        <div class="tableTitle_right">
            <el-button  @click="editSelf()"></el-button>
            <el-button  @click="dialogFormVisible = true"></el-button>
        </div>
    </div>
        <el-table :data="tableData" border height="calc(100vh - 360px)" style="width: 100%" class="tableData" empty-text="">
            <el-table-column prop="level" label="" sortable>
                <template #default="{ row }">
                    Lv{{ row.level? `${row.level.level} ${row.level.levelTitle}`:''}}
                </template>
            </el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="lastOnline" label="" sortable></el-table-column>
            <el-table-column prop="fingerprint" label="">
                <template #default="{ row }">
                    <div class="fingerprint" @click="copyFingerPrint(row.fingerprint)">
                        {{ row.fingerprint }}
                    </div>
                </template>
            </el-table-column>
            <el-table-column prop="status" label="" sortable>
                <template v-slot="scope">
                    <div :class="scope.row.status?'valid':'invalid'">{{ scope.row.status?'':'' }}</div>
                </template>
            </el-table-column>
            <el-table-column label="" width="250">
                <template v-slot="scope">
                    <div class="btn">
                        <el-button class="btn_link" @click="editUser(scope.row.idx)"></el-button>
                        <el-button class="btn_link" type="warning" @click="stopUser(scope.row.account,scope.row.idx)">{{scope.row.status?'':''}}</el-button>
                        <el-button class="btn_link" type="danger" @click="deleteUser(scope.row.account,scope.row.idx)"></el-button>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="">
                    <el-input v-model="form.account"  placeholder=" Visitor" autocomplete="off" clearable></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.name" autocomplete="off" clearable></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.password" autocomplete="off" show-password clearable></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"></el-button>
                <el-button type="primary" @click="create()"></el-button>
            </div>
        </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
import jsCookie from 'js-cookie'
export default {
    name:'ClientTable',
    data(){
        return{
            tableData: [],
            dialogFormVisible:false,
            form:{
                account:'',
                password:'',
                name:'',
                type:'student'
            },
        }
    },
    mounted(){
        this.getData()
    },
    methods:{
        async getData(){
            const res = await axios.get('/api/infoPage/getStudent',{
                headers:{
                    'x-user-token':jsCookie.get('authToken')
                }
            })
            if(res.data.students) {
                this.tableData = res.data.students
                this.$bus.$emit('setStudentNum',this.tableData.length)
            }
            else this.$bus.$emit('handleAlert','',res.data.message,res.data.type)
        },
        async create(){
            const res = await axios.post('/api/infoPage/createStudent',this.form,{
                headers:{
                    'x-user-token':jsCookie.get('authToken')
                }
            })
            if(res.data.type == 'success'){
                this.getData();
                this.dialogFormVisible = false;
                this.form.account = '';
                this.form.password = '';
                this.form.name = '';
                this.$bus.$emit('handleAlert','',res.data.message,res.data.type)
            }
            else this.$bus.$emit('handleAlert','',res.data.message,res.data.type)
        },
        async deleteUser(account, idx){
            try{
                await this.$confirm(` (${account})?`, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                })
                const res = await axios.delete(`/api/infoPage/deleteStudent/${idx}`,{
                    headers:{
                        'x-user-token':jsCookie.get('authToken')
                    }
                })
                if(res.data.type == 'success'){
                    this.getData();
                    this.$bus.$emit('getUsageMemory')
                    this.$bus.$emit('handleAlert','',res.data.message,res.data.type)
                }
                else this.$bus.$emit('handleAlert','',res.data.message,res.data.type)
            }
            catch(e){}
        },
        async stopUser(account, idx){
            try{
                await this.$confirm(` (${account}) ?`, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                })
                const res = await axios.put(`/api/infoPage/stopStudent/${idx}`,{},{
                    headers:{
                        'x-user-token':jsCookie.get('authToken')
                    }
                })
                if(res.data.type == 'success'){
                    this.getData();
                    this.$bus.$emit('handleAlert','',res.data.message,res.data.type)
                }
                else this.$bus.$emit('handleAlert','',res.data.message,res.data.type)
            }
            catch(e){}
        },
        async editSelf(){
            this.$router.push({ path: '/academic/modifyInfo', query: { idx: this.$bus.$currentUser.idx } }).catch(e => {})
        },
        async editUser(idx){
            this.$router.push({ path: '/academic/modifyInfo', query: { idx:idx } }).catch(e => {})
        },
        async copyFingerPrint(text){
            this.$bus.$emit('copyToClipboard','',text);
        }
    }
}
</script>

<style scoped>
    .table{
        width: 95%;
        margin: 0 auto;
        margin-top: 20px;
    }
    .tableTitle{
        line-height: 85px;
        text-align: center;
        display: flex;
        align-items: center;
    }
    .tableTitle_left{
        float: left;
        font-size: 24px;
    }
    .tableTitle_right{
        height: 40px;
        text-align: center;
        /*  */
        margin-left: auto;
        font-size: 14px;
        display: flex;
        align-items: center;
    }
    .tableTitle_right:hover{
        cursor: pointer;
        color: blue;
    }
    .valid{
        color: lightgreen;
    }
    .invalid{
        color: rgb(170, 170, 170);
    }
    .btn{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }
    .btn_link{
        font-size: 14px;
    }
    .btn_link:hover{
        cursor: pointer;
    }
    .fingerprint {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1; /*  */
        overflow: hidden;
        text-overflow: ellipsis;
        word-break: break-all; /*  */
    }
</style>